/* MY CSS STYLES */

.box-container {
  /* background-color: aliceblue; */
  /* dimensions: */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 75vh;
  /* font: */
  font-family: 'Spectral', serif;
  font-weight: 400;
  font-size: 1rem;
  margin: 0rem 1rem 0rem 1rem;
}

.results-content {
  min-width: 8rem;
  max-width: 12rem;
  margin: 0.5rem;
}

.box-content {
  background-color: rgb(244, 244, 244);
  border-radius: 10px;
  max-width: 36rem;
  width: 100%;
  padding: 1.5rem 1.5rem 0.5rem 1.5rem;
}

.box-content input {
  margin: 1rem 1rem 1rem 1rem;
  border-radius: 10px;
  border-width: 0.01rem;
  height: 2rem;
  width: 60%;
  /* font */
  font-family: 'Spectral', serif;
  font-size: 1rem;
  font-weight: 300;
  text-align: center;
}

.box-content input:focus {
  outline-color: rgb(95, 172, 255)5, 235, 235;
}

.redo-button {
  width: 3.5rem;
  height: 1.8rem;
  margin: 0.5rem;
  background-color: rgb(225, 225, 225);

  border-radius: 5px;
  border: none;

  font-family: 'Spectral', serif;
  font-size: 16px;
  font-weight: 400;
  color: rgb(48, 48, 48);
}

.num-button {
  padding-bottom: 24px;
  margin: 0.25rem;
  padding-left: 0px;
  background-color: rgba(255, 255, 255, 0);
  border-style: none;
  width: 16px;
  height: 18px;

  font-family: 'Spectral', serif;
  font-size: 16px;
  font-weight: 400;
  color: rgb(48, 48, 48);
}

.redo-button:hover,
.num-button:hover {
  cursor: pointer;
}

.selected-num {
  text-decoration: underline;
  /* font-weight: 600; */
}